.table {
  width: 70%;
  text-align: center;
  margin: 0 auto;
  box-shadow: 0px 0px 12px #18191b;
  border-radius: 8px;
  font-weight: 700;

  &__item {
    background-color: #26272b;
    box-shadow: 0px 0px 12px #18191b;
    border-radius: 8px;
    cursor: pointer;
    color: #FFFFFF;
    transition: all .3s linear;
  }
  &__item:hover {
    background-color: #C1C4C6;
    color: #18191b;
  }
  &__td {
    padding: 10px;
  }
  &__img {
    width: 20px;
    height: 20px;

  }
}